___CSS_LOADER_EXPORT___.push([module.id, `body {
    background-color: var(--md-sys-bg-landing);
    color: var(--md-sys-color-on-surface-variant);;
}

.landing-bg {
    position: relative;
    width: 100%;
    height: auto;
    min-height: 35rem;
    padding: 15rem 0;
    overflow: hidden;
}

hr.divider {
    max-width: 3.25rem;
    border-width: 0.2rem;
    border-color: #f4623a;
    opacity: 1;
}

hr.divider-light {
    border-color: #fff;
}

.me {
    color: white;
    font-size: 4rem;
    text-align: center;
    text-transform: uppercase;
    color: #219F83;

}

.job-title {
    margin-top: 20px;
    text-align: center;
}

section {
    padding-left: 10px;
    padding-top: 3rem;
    padding-bottom: 3rem;
}

footer {
    background-color: var(--md-sys-color-surface);
    color: var(--md-sys-color-on-surface-variant);
    font-weight: 400;
}

.copyright, .copyright > * {
    font-family: var(--md-sys-typescale-footer-copyright-family-name);
}

footer a {
    color: var(--md-sys-color-on-surface-variant);
}

.sharethis-inline-share-buttons {
    margin-top: 10px;
}

.st-total {
    margin-right: 20px !important;
    color: var(--md-sys-color-on-surface-variant) !important;
}

.contact-section {
    background-color: var(--md-sys-color-surface);
    color: var(--md-sys-color-on-surface-variant);
}

.projects-section {
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-color: var(--md-sys-color-surface);
    color: var(--md-sys-color-surface);
}

.circle {
    height: 500px;
    width: 500px;
    border-radius: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
}

.triangle {
    width: 0;
    height: 0;
    border-left: 150px solid transparent;
    border-right: 150px solid transparent;
    border-bottom: 250px solid #416277;
    position: absolute;
    overflow: hidden;
    top: 93%;
    left: 25%;
    transform: translate(-50%, -50%);
}

.circle, .triangle {
    position: absolute;
    z-index: -1;
}

.circle-green {
    background-color: var(--md-sys-color-landing-circle1);
    top: 10%;
    left: 25%;
    height: 350px;
    width: 350px;
}

.circle-blue {
    background-color: var(--md-sys-color-landing-circle2);
    top: 3%;
    left: 45%;
    height: 450px;
    width: 450px;
}

.circle-red {
    background-color: var(--md-sys-color-landing-circle3);
    top: 50%;
    left: 105%;
}


.card {
    /*background-color: var(--md-sys-color-surface);*/
    border-radius: 8px;
    box-shadow: var(--md-sys-color-shadow);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background-color: transparent;
    border: none;
    border-radius: 20px;
    box-shadow: 0px 1px 2px 0px rgb(0 0 0 / 30%), 0px 2px 6px 2px rgb(0 0 0 / 15%);
}

.card-header {
    padding: 16px;
}

.card-title {
    color: var(--md-sys-color-on-surface);
    font-size: 20px;
    font-weight: 600;
    margin: 0;
}

.card-content {
    padding: 16px;
}

.card-text {
    color: var(--md-sys-color-on-surface);
    font-size: 14px;
    margin: 0;
}

.card-img {
    border-radius: 20px;
}

.img-project {
    max-width: 170px;
    max-height: 150px;
    min-height: 150px;
    padding: 2px;
    margin: 3px;
    border-radius: 20px;
    background-color: var(--md-sys-color-tertiary);
}

.badge-project {
    background-color: var(--md-sys-color-secondary-container);
    color: var(--md-sys-color-secondary);
}

.collapsible {
    margin-bottom: 10px;
    border-radius: 4px;
}

.collapsible-header {
    cursor: pointer;
    background-color: var(--md-sys-color-surface-tint);
    color: var(--md-sys-color-surface);
    padding: 12px;
    border-radius: 20px 20px 0 0;
    margin-top: 10px;
}

.collapsible-header.collapsed {
    border-radius: 20px 20px 20px 20px;
    transition-duration: 1s;
}

.collapsible-header:hover {
    background-color: var(--md-sys-color-surface-tint);
}

.collapsible-header i.material-icons {
    font-size: 24px;
}

.collapsible-body {
    padding: 12px;
    background-color: var(--md-sys-color-surface);
    color: var(--md-sys-color-on-surface);
    border: none;
    border-radius: 0 0 20px 20px;
    margin-bottom: 10px;
}

#g-recaptcha-response {
    display: block !important;
    position: absolute;
    margin: -50px 0 0 0 !important;
    z-index: -999999;
    opacity: 0;
}
