    position: fixed;
    top: 0;
    left: 0;
    width: 250px;
    height: 100vh;
    z-index: 1;
    overflow-y: auto;
    overflow-x: hidden;
    transition: all 0.3s ease;
    padding: 8px;
    border-radius: 0px 16px 16px 0px;
    background-color: var(--md-sys-color-sidebar-background);
    box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, .3), 0px 2px 6px 2px rgba(0, 0, 0, .15);
}

nav.fixed-top {
    background-color: var(--md-sys-color-sidebar-background);
}

nav.fixed-top a {
    color: var(--md-sys-color-on-surface-variant)
}

nav.fixed-top .navbar-toggler {
    background-color: var(--md-sys-color-sidebar-background);
    color: var(--md-sys-color-on-surface-variant)
}

nav.fixed-top .navbar-toggler:focus {
    box-shadow: none;
}

#sidebar-wrapper::-webkit-scrollbar {
    width: 5px;
    background-color: #f5f5f5;
}

#sidebar-wrapper::-webkit-scrollbar-thumb {
    background-color: #000000;
}

#sidebar-wrapper .sidebar-heading img {
    border-radius: 50px;
    width: 70px;
}

#sidebar-wrapper .list-group-item {
    border: none;
}

#sidebar-wrapper .list-group-item:hover {
    background-color: #c2c2c2;
}

#page-content-wrapper {
    width: 100%;
    margin-left: 230px;
    padding-left: 10px;
    transition: all 0.3s ease;
    background-color: transparent;
}

#wrapper.toggled #sidebar-wrapper {
    width: 80px;
}

#wrapper.toggled #page-content-wrapper {
    padding-left: 80px;
}


#sidebar-wrapper > .list-group > a.list-group-item, #sidebar-wrapper > .list-group > div.list-group-item {
    color: #444746;
    color: var(--md-sys-color-on-surface-variant);
    padding: 2px 16px;
    border-radius: 100px;
    height: 48px;
    display: flex;
    position: relative;
    align-items: center;
    background-color: transparent;
}

#sidebar-wrapper > .list-group > a.list-group-item:hover, #sidebar-wrapper > .list-group > div.list-group-item:hover {
    background-color: #E5E8ED !important;
    color: var(--md-sys-color-shadow-dark);
}

#sidebar-wrapper > .list-group > a.list-group-item:active, #sidebar-wrapper > .list-group > div.list-group-item:active {
    background-color: #c2e7ff !important;
}

#sidebar-wrapper > .list-group > div.list-group-item > a.dropdown-toggle {
    color: #444746;
    text-decoration: none;
}

.dropdown-item:active {
    background-color: #c2e7ff !important;
    color: #444746;
}

.dark-mode-switch-container {
    display: flex;
    position: absolute;
    bottom: 0;
    align-items: center;
    justify-content: center;
    width: 93%;
    height: 80px;
}


.mode-toggle {
    border: none;
    outline: none;
    border-radius: 100%;
    color: var(--md-sys-color-on-surface-variant);
    border: 1px solid var(--md-sys-color-outline);
    background-color: var(--md-sys-color-sidebar-background);;
    font-size: 24px;
    width: 48px;
    height: 48px;
    border-radius: 100px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-projects {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100%;
    max-width: 1760px;
    margin: 0 auto;
    transition-duration: 300ms;
    transition-property: max-width, margin;
    transition-timing-function: cubic-bezier(0.2, 0, 0, 1);
    z-index: 1;
}

.navigation-container {
    display: flex;
    justify-content: center;
    height: 88px;
    margin: 0 8px;
    padding-top: 9px;
    gap: 8px;
    transition: height 300ms cubic-bezier(0.2, 0, 0, 1), opacity 200ms cubic-bezier(0.2, 0, 0, 1);
    border-radius: 0 0 40px 40px;
    opacity: 1;
}

.tabbed-navigation {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    height: 100%;
    border-radius: 40px;
    background: var(--md-sys-color-surface);
    text-align: center;
    white-space: nowrap;
    overflow: auto;
}

.tab {
    display: inline-flex;
    position: relative;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    padding: 0 24px;
    border: none;
    border-radius: 40px;
    background: var(--md-sys-color-surface);
    color: var(--md-sys-color-on-surface);
    line-height: 44px;
    text-decoration: initial;
    cursor: pointer;
    vertical-align: middle;
}

.tab .material-symbols-sharp {
    margin-right: 8px;
    transition: font-variation-settings 200ms cubic-bezier(0.2, 0, 0, 1);
    font-variation-settings: "wght" 400, "opsz" 24;
    z-index: 1;

}


.tab::before {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    transform: scaleX(0.32);
    transition-duration: 200ms;
    transition-property: transform, opacity;
    transition-timing-function: cubic-bezier(0.2, 0, 0, 1);
    border-radius: 40px;
    background: var(--md-sys-color-tertiary);
    content: "";
}

.tab:hover {
    background: var(--md-sys-color-on-surface-variant);
    color: var(--md-sys-color-on-tertiary);
    text-decoration: none;
}

.tab.active::before {
    opacity: 1;
    transform: scaleX(1);
}

.tab.active > .tab-label, .tab.active > .material-symbols-sharp {
    color: var(--md-sys-color-on-primary);
    font-variation-settings: "GRAD" 125;
    z-index: 5;
}

.tab.active[_ngcontent-djl-c44]:hover::before {
    background: var(--md-sys-color-on-surface-variant);
}

nav.fixed-top {
    display: none;
}

.navbar{
    transition: transform 0.3s ease-in-out;
}

.navbar.navbar-hidden {
    transform: translateY(-100%);
    transition: transform 0.3s ease-in-out;
}

.navbar-toggler {
    border: none;
}

.navbar-toggler.collapsed {
    margin-top: 0;
}

.navbar-toggler .material-symbols-sharp {
    font-size: 30px;
}

.navbar-toggler {
    margin-top: 10px;
